<template>
	<div>
		<sidebar scroll-to-speed="750"/>
		<title-section scroll-to-speed="750"/>
		<experiences-section/>
		<!-- <projects-section/> -->
		<!-- <contact-me-section/> -->
	</div>
</template>

<script>
	import Sidebar from './sidebar/Sidebar'
	import TitleSection from './landingPageSections/TitleSection'
	import ExperiencesSection from './landingPageSections/ExperiencesSection'
	//import ProjectsSection from './landingPageSections/ProjectsSection'
	//import ContactMeSection from './landingPageSections/ContactMeSection'
	export default {
		name: "LandingPage",
		components: {
			Sidebar,
			TitleSection,
			ExperiencesSection,
			//ProjectsSection,
			//ContactMeSection
		}
	}
</script>

<style lang="scss">
	@import "../styles/global.scss";
	@import "../styles/text_mixins.scss";
	@import "../styles/transition_mixins.scss";
	@import "../styles/position_mixins.scss";
	@import "../styles/dimension_mixins.scss";
	@import "../styles/background_mixins.scss";

	.scrollto_next {
		@include text(
			$color:rgba( 255, 255, 255, 0.8 ),
			$decoration: none
		);
		@include background(
			$color: rgba( 0, 0, 0, 0.4 ),
			$border-radius: 50%
		);
		margin-bottom: 50px;
		outline: 0;
		@include dimensions( 75px, 75px );
		@include position( flex, absolute, $bottom: 0px );
		@include content_position( center, center );
		@include transition( 0.2s ease );
		&:hover {
			color: $accent;
			background-color: rgba( 0, 0, 0, 0.8 );
			margin-bottom: 40px;
			@include transition( 0.1s ease );
		}
	}
	.section {
		display: flex;
		justify-content: center;
		& .section_content {
			width: 66%;
		}
	}
	@media (min-width: 300px) and (max-width: 500px) {
		.sidebar {
			visibility: hidden;
		}
		.section {
			display: flex;
			justify-content: center;
			& .section_content {
				width: 100%;
			}
		}
	}
	@media (min-width: 500px) and (max-width: 732px) {
		.scrollto_next {
			right: 0px;
			margin-right: 50px;
		}
	}
</style>


