<template>
	<div id="experiences_section" class="section">
		<div class="flex_container centered_horizontally" >
			<div class="title_container section_content">
				<header>
					<h2 class="title">
						Experiences
					</h2>
				</header>
				<section class="expertise_list">
					<div v-for="experience in experiences" :key="experience.title" class="expertise">
						<h2>{{experience.title}}</h2>
						<p v-for="example in experience.examples" :key="example">
							{{example}}
						</p>
					</div>
				</section>
			</div>
		</div>
		<div class="flex_container centered_horizontally grey_background" >
			<div class="section_content">
				<section>
					<div class="company_list_container">
						<ul class="company_list">
							<li v-for="internship in internships" :key="internship.url"
								:style="{
									backgroundImage: internship.backgroundImage,
									backgroundColor: internship.backgroundColor
								}"
							>
								<a :href=internship.url>
									<div>
										<img :src=internship.img alt="">
									</div>
								</a>
							</li>
						</ul>
					</div>
				</section>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "LandingPageExperiencesSection",
		data () {
			return {
				experiences: [
					{
						title: "Web Development",
						examples: [
							"Internships in Full-Stack Development",
							"Node.js, Ruby on Rails, Python and PHP",
							"Dev-Ops",
						]
					},
					{
						title: "Mobile Development",
						examples: [
							"Android Apps With Java and Socket.io",
							"Cross Platform Application With React Native",
						]
					},
					{
						title: "Native Development",
						examples: [
							"Implemented OS features in NachOS",
							"Projects in 6502 Micro-Processor and Spark assembly",
							"OpenGL Applications",
						]
					},
				],
				internships: [
					{
						url: "https://www.boreal-is.com/fr/",
						img: "/static/images/borealis_md.png",
						backgroundImage: "url(/static/images/borealis_bg.jpg)",
						backgroundColor: "black",
					},
					{
						url: "https://www.rtings.com/",
						img: "/static/images/rtings_md.png",
						backgroundImage: "url(/static/images/rtings_bg.jpg)",
						backgroundColor: "black",
					},
					{
						url: "https://www.metareal.com/",
						img: "/static/images/metareal_md.png",
						backgroundImage: "url(/static/images/metareal_bg.jpg)",
						backgroundColor: "black",
					},
					{
						url: "https://www.mirego.com/",
						img: "/static/images/mirego_md.png",
						backgroundImage: "",
						backgroundColor: "black",
					},
				],
			}
		},
	}
</script>

<style lang="scss" scoped>
	@import "../../styles/global.scss";
	@import "../../styles/text_mixins.scss";
	@import "../../styles/position_mixins.scss";
	@import "../../styles/spacing_mixins.scss";
	@import "../../styles/dimension_mixins.scss";
	@import "../../styles/transition_mixins.scss";
	@import "../../styles/background_mixins.scss";

	.flex_container {
		display: flex;
		&.centered_horizontally {
			justify-content: center;
		}
		&.centered_vertically {
			align-content: center;
		}
	}
	.grey_background {
		background-color: rgb( 15, 15, 15 );
	}
	.section {
		flex-direction: column;
		justify-content: center;
		color: black;
		background-color: white;
		& .section_content {
			&.title_container {
				@include spacing( 0px, 50px 0px 50px 0px );
				& header {
					display: flex;
					flex-direction: column;

					@include content_position( center, center );
					hr {
						border-top: 2px black solid;
					}
					& .title {
						@include text(
							$transform: uppercase,
							$font-family: "Barlow Condensed",
							$font-size: 3em,
							$font-weight: 400
						);
						border-bottom: 2px solid;
						padding: 0 32px 10px;

						& i {
							@include transition( all 0.1s ease );
						}

						&:hover {
							& i {
								margin: 0px 15px 0px 0px;
								@include transition( all 0.1s ease );
							}
						}
					}
				}
			}
		}
	}
	.expertise_list {
		display: flex;
		flex-flow: row wrap;
		@include spacing( 0px, 0px 0px 50px 0px );
		@include content_position( flex-start, space-around );
		& .expertise {
			flex: 1;
			text-align: center;
			@include min-dimensions( 45%, 150px );
			margin: 0px 16px 16px;
			& p {
				color: dimgray;
			}
		}
	}
	.company_list_container {
		@include position( $position: relative, $top: -50px );
		& ul.company_list {
			display: flex;
			flex-flow: row wrap;
			list-style: none;
			@include background(
				$box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.3)
			);
			@include spacing( 0px, 0px );
			& li {
				flex: 1;
				background-size: 175% auto;
				@include transition( all 0.2s ease );
				@include min-dimensions( $height: 250px, $width: 300px );
				@include background(
					$box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.5),
					$color: rgba(0, 0, 0, 0.50)
				);

				& a {
					outline: 0;
				}
				&:hover {
					z-index: 99;
				}

				& div {
					display: flex;
					height: 100%;
					background-color: rgba(0, 0, 0, 0.6);
					@include spacing( 0px, 0px 25px 0px 25px );
					@include content_position( center, center );
					@include transition( all 0.2s ease );
					&:hover {
						@include background(
							$box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.5),
							$color: rgba(0, 0, 0, 0.50)
						);
						@include transition( all 0.2s ease );
					}
				}

				& img {
					image-rendering: auto;
					transition: max-width 0.2s linear;
					max-width: 200px;
				}
			}
		}
	}

	@media (max-width: 481px) {
		.expertise_list {
			& .expertise {
				@include min-dimensions( 100%, 150px );
			}
		}
	}
</style>


